import { createApp } from "vue";
import { createPinia } from "pinia";
import { createWebHashHistory, createRouter } from "vue-router";
import "./posts.css";

// Import store
import { useAppStore } from "@/store/app/app.js";

// Import comps
import AppWrapper from "@/pages/posts/index.vue";

// Build app
const app = createApp(AppWrapper);

// Use pinia
const pinia = createPinia();
app.use(pinia);

// Get data attributes
const scriptTag = document.querySelector("#uipc-script");
const pluginBase = scriptTag.getAttribute("plugin-base");
const restBase = scriptTag.getAttribute("rest-base");
const restNonce = scriptTag.getAttribute("rest-nonce");
const adminUrl = scriptTag.getAttribute("admin-url");
const siteURL = scriptTag.getAttribute("site-url");
const userID = scriptTag.getAttribute("user-id");
const userName = scriptTag.getAttribute("user-name");
const userEmail = scriptTag.getAttribute("user-email");
let frontPage = scriptTag.getAttribute("front-page");
frontPage = frontPage == "true" ? true : false;

let uipcSettings = scriptTag.getAttribute("uixpress-settings");
uipcSettings = JSON.parse(uipcSettings);

let postTypes = scriptTag.getAttribute("post_types");
postTypes = JSON.parse(postTypes);

let mime_types = scriptTag.getAttribute("mime_types");
mime_types = JSON.parse(mime_types);

// Update app store
const appStore = useAppStore();
appStore.updateState("pluginBase", pluginBase);
appStore.updateState("restBase", restBase);
appStore.updateState("restNonce", restNonce);
appStore.updateState("adminUrl", adminUrl);
appStore.updateState("siteURL", siteURL);
appStore.updateState("userID", userID);
appStore.updateState("userName", userName);
appStore.updateState("userEmail", userEmail);
appStore.updateState("frontPage", frontPage);
appStore.updateState("uixpress_settings", uipcSettings);
appStore.updateState("postTypes", postTypes);
appStore.updateState("mimeTypes", mime_types);

const pluginTag = document.querySelector("#uipc-posts-script");

let supports_categories = pluginTag.getAttribute("supports_categories");
supports_categories = supports_categories === "1" ? true : false;
appStore.updateState("supports_categories", supports_categories);

let supports_tags = pluginTag.getAttribute("supports_tags");
supports_tags = supports_tags === "1" ? true : false;
appStore.updateState("supports_tags", supports_tags);

let postStatuses = pluginTag.getAttribute("post_statuses");
postStatuses = postStatuses ? JSON.parse(postStatuses) : [];
appStore.updateState("postStatuses", postStatuses);

// Declare translation functions
app.config.globalProperties.__ = __;
app.config.globalProperties.sprintf = sprintf;

app.mount("#uix-posts-page");
