<script setup>
import { ref, watch, nextTick, computed, watchEffect, defineAsyncComponent, onMounted, defineEmits } from "vue";
import { ShadowRoot } from "vue-shadow-dom";

// Store
import { useAppStore } from "@/store/app/app.js";
const appStore = useAppStore();

// Funcs
import { inSearch } from "@/assets/js/functions/inSearch.js";
import { lmnFetch } from "@/assets/js/functions/lmnFetch.js";
import { notify } from "@/assets/js/functions/notify.js";
import { useDarkMode } from "./src/useDarkMode.js";
import { fetchPostsData } from "./src/fetchPostsData.js";
import { batchDuplicatePosts, duplicating } from "./src/batchDuplicatePosts.js";
import { batchDeletePosts, deleting } from "./src/batchDeletePosts.js";
import { batchPublishPosts, publishing } from "./src/batchPublishPosts.js";
import { inlineTitleUpdate } from "./src/inlineTitleUpdate.js";
import { updatePostAuthor } from "./src/updatePostAuthor.js";
import { updatePostStatus } from "./src/updatePostStatus.js";
import { returnPostData } from "./src/returnPostData.js";
const { isDark } = useDarkMode();

// Comps
import LoadingIndicator from "@/components/utility/loading-indicator/index.ts";
import Notifications from "@/components/utility/notifications/index.vue";
import AppButton from "@/components/utility/app-button/index.vue";
import AppIcon from "@/components/utility/icons/index.vue";
import AppToggle from "@/components/utility/toggle/index.vue";
import Confirm from "@/components/utility/confirm/index.vue";
import AppTable from "@/components/utility/table/index.vue";
import PostActionList from "./src/post-actions.vue";
import InlineUserSelect from "./src/inline-user-select.vue";
import InlineStatusSelect from "./src/inline-status-select.vue";
import InlineImageSelect from "./src/inline-image-select.vue";
import BatchEdit from "./src/batch-edit.vue";
import TableFilters from "./src/table-filters.vue";

// Shared constants
import {
  selected,
  posts,
  loading,
  pagination,
  filterOptions,
  activeFilter,
  columns,
  column_classes,
  custom_styles,
  currentPostType,
  confirm,
  rowActions,
  suppresedLoading,
  openPostParents,
  expandAllChildren,
} from "./src/constants.js";

// Set post type
const urlParams = new URLSearchParams(window.location.search);
const queryPostType = urlParams.get("post_type");
currentPostType.value = appStore.state.postTypes["post"];

if (queryPostType) {
  currentPostType.value = appStore.state.postTypes[queryPostType] ? appStore.state.postTypes[queryPostType] : currentPostType.value;
}

pagination.value.post_type = currentPostType.value.name;

// Ordering for pages
if (pagination.value.post_type === "page") {
  pagination.value.orderby = "title";
  pagination.value.order = "ASC";
}

// Refs
const adoptedStyleSheets = ref(new CSSStyleSheet());
const postActions = ref([]);
const teleportArea = ref(false);

/**
 * Injects CSS styles into the shadow root of the component
 * @function setStyles
 * @since 0.0.1
 */
const setStyles = () => {
  let appStyleNode = document.querySelector("#uixpress-posts-css");
  if (!appStyleNode) {
    appStyleNode = manuallyAddStyleSheet();
    // Wait for stylesheet to load
    appStyleNode.onload = () => {
      const appStyles = appStyleNode.sheet;
      for (const rule of [...appStyles.cssRules].reverse()) {
        adoptedStyleSheets.value.insertRule(rule.cssText);
      }
    };
  } else {
    const appStyles = appStyleNode.sheet;
    for (const rule of [...appStyles.cssRules].reverse()) {
      adoptedStyleSheets.value.insertRule(rule.cssText);
    }
  }
};

const postsAndChildrenCount = computed(() => {
  const countPosts = (postsArray) => {
    let count = 0;

    postsArray.forEach((post) => {
      // Count the current post
      count++;

      // If post has children, recursively count them
      if (post.children?.length > 0) {
        count += countPosts(post.children);
      }
    });

    return count;
  };

  return countPosts(posts.value);
});

/**
 * Creates and adds a stylesheet link element to the document head
 * @function manuallyAddStyleSheet
 * @returns {HTMLLinkElement} The created link element
 * @since 0.0.1
 */
const manuallyAddStyleSheet = () => {
  var link = document.createElement("link");
  link.rel = "stylesheet";
  link.href = `${appStore.state.siteURL}/wp-content/plugins/uixpress/app/dist/assets/styles/posts.css`;
  document.head.appendChild(link);
  return link;
};

/**
 * Computed property that returns formatted column configurations
 * @computed returnColumns
 * @returns {Array<Object>} Array of column objects with formatting and display properties
 * @property {string} key - Column identifier
 * @property {string} label - Display label for the column
 * @property {boolean} active - Whether the column is active
 * @property {string} classes - CSS classes for the column
 * @property {string} column_classes - Additional CSS classes from column_classes ref
 * @property {string} sort_key - Key used for sorting the column
 */
const returnColumns = computed(() =>
  columns.value
    .filter(({ key }) => key !== "cb")
    .map(({ key, label, active, sort_key }) => ({
      key,
      label,
      active,
      sort_key,
      classes: `${key} column-${key}`,
      column_classes: column_classes.value?.[key]?.join(" ") ?? "",
    }))
);

/**
 * Handles opening a post with different behaviors based on modifier keys
 * @function openPost
 * @param {Event} evt - The click event
 * @param {number} index - Index of the post in the posts array
 */
const openPost = (evt, index) => {
  const postItem = posts.value[index];
  if (!postItem) return;

  const url = postItem.is_editable ? postItem.edit_url : postItem.view_url;

  // Handle different click types
  if (evt.metaKey || evt.ctrlKey) {
    // Command/Control click -> Open in new tab
    window.open(url, "_blank");
  } else if (evt.shiftKey) {
    // Shift click -> select post
    if (selected.value.includes(postItem.id)) {
      const selectedIndex = selected.value.findIndex((item) => item == postItem.id);
      selected.value.splice(selectedIndex, 1);
    } else {
      selected.value.push(postItem.id);
    }
  } else if (evt.altKey) {
    // Alt click -> Open in new tab in background (where supported)
    window.open(url, "_blank").blur();
    window.focus();
  } else {
    // Normal click -> Open in same tab
    window.location.href = url;
  }
};

/**
 * Navigates to the first page of posts and fetches data
 * @function handleFirstPage
 */
const handleFirstPage = () => {
  pagination.value.page = 1;
  fetchPostsData(true);
};

/**
 * Navigates to the previous page of posts and fetches data
 * @function handlePreviousPage
 */
const handlePreviousPage = () => {
  if (pagination.value.page > 1) {
    pagination.value.page--;
    fetchPostsData(true);
  }
};

/**
 * Navigates to the next page of posts and fetches data
 * @function handleNextPage
 */
const handleNextPage = () => {
  if (pagination.value.page < pagination.value.pages) {
    pagination.value.page++;
    fetchPostsData(true);
  }
};

/**
 * Navigates to the last page of posts and fetches data
 * @function handleLastPage
 */
const handleLastPage = () => {
  pagination.value.page = pagination.value.pages;
  fetchPostsData(true);
};

/**
 * Computed property that returns the post type query parameter if it exists
 * @computed maybeReturnPostType
 * @returns {string} Query string with post type or empty string
 */
const maybeReturnPostType = computed(() => {
  return queryPostType ? `?post_type=${queryPostType}` : "";
});

const handleRightClick = (evt, index) => {
  const currentPost = posts.value[index];
  if (!currentPost) return;

  rowActions.value[currentPost.id].show(evt);
};

const setRowActions = (el, index, postID) => {
  if (el) {
    rowActions.value[postID] = el;
  }
};

/**
 * Updates URL parameters based on the active filter and triggers data fetch
 * @returns {Promise<void>}
 */
const handleFilterChange = async () => {
  // Reset pagination to first page
  pagination.value.page = 1;

  // Create URL object for manipulation
  const url = new URL(window.location);
  url.search = ""; // Clear existing search params

  if (activeFilter.value !== "all" && filterOptions.value?.[activeFilter.value]) {
    await updateUrlWithFilterParams(url);
  }

  // Update browser history
  updateBrowserHistory(url);

  // Wait for Vue to update the DOM
  await nextTick();

  // Fetch new data
  return fetchPostsData();
};

/**
 * Updates URL with filter parameters
 * @param {URL} url - URL object to update
 * @returns {Promise<void>}
 */
const updateUrlWithFilterParams = async (url) => {
  const active_view = filterOptions.value[activeFilter.value];
  if (!active_view?.query_params) return;

  // Create a new URLSearchParams object with the query parameters
  const params = new URLSearchParams();

  Object.entries(active_view.query_params).forEach(([key, value]) => {
    const paramValue = Array.isArray(value) ? value.join(",") : value;
    params.set(key, paramValue);
  });

  url.search = params.toString();
};

/**
 * Updates browser history with new URL
 * @param {URL} url - URL to update history with
 */
const updateBrowserHistory = (url) => {
  window.history.replaceState(
    {
      path: url.toString(),
    },
    "",
    url
  );
};

const postExpanded = (post) => {
  return openPostParents.value.includes(post.id);
};

const togglePostExpanded = (post) => {
  const isOpen = postExpanded(post);

  if (isOpen) {
    const foundIndex = openPostParents.value.findIndex((id) => id == post.id);
    openPostParents.value.splice(foundIndex, 1);
  } else {
    openPostParents.value.push(post.id);
  }
};

const returnChildLength = (post) => {
  // If the object is null or undefined, return 0
  if (!post) return 0;

  // If there are no children, return 0
  if (!post.children || !Array.isArray(post.children)) return 0;

  // Count immediate children
  let count = post.children.length;

  // Recursively count children of each child
  for (const child of post.children) {
    count += returnChildLength(child);
  }

  return count;
};

/**
 * Watch effect for pagination search changes
 * Fetches posts data when search is cleared
 */
watch(
  () => pagination.value.search,
  (newVal, oldVal) => {
    if (!newVal && oldVal) {
      fetchPostsData();
    }
  }
);

/**
 * Watch effects for various pagination properties
 * Each triggers a fetch of posts data when changed
 */
watch(() => pagination.value.per_page, fetchPostsData);
watch(() => pagination.value.order, fetchPostsData);
watch(() => pagination.value.orderby, fetchPostsData);
watch(() => pagination.value.dateRange, fetchPostsData);
watch(() => pagination.value.categories, fetchPostsData);
watch(() => activeFilter.value, handleFilterChange);

fetchPostsData();
setStyles();
</script>

<template>
  <component is="style"> html{font-size:14px;}#wpcontent{padding:0}</component>
  <ShadowRoot tag="div" :adopted-style-sheets="[adoptedStyleSheets]" class="uipx-normalize">
    <Notifications />
    <div class="translate-x-0">
      <LoadingIndicator :height="3" />
    </div>

    <template v-for="item in custom_styles">
      <link rel="stylesheet" :id="`${item.handle}-css`" :href="item.src" media="all" />
    </template>

    <div
      class="@container bg-white dark:bg-zinc-900 uipx-normalize font-sans dark:bg-zinc-900 bg-white p-6 flex flex-col gap-6 text-zinc-500 dark:text-zinc-400"
      style="min-height: calc(100dvh - var(--wp-admin--admin-bar--height))"
      :class="isDark ? 'dark' : ''"
      id="uixpress-posts-wrap"
    >
      <div class="flex flex-row place-content-between col-span-3 items-center mb-6">
        <div class="text-2xl font-semibold text-zinc-900 dark:text-zinc-100">{{ currentPostType?.labels?.name || __("Posts", "uixpress") }}</div>
      </div>

      <div class="flex flex-row place-content-between">
        <AppToggle :options="filterOptions" v-model="activeFilter" class="w-auto" style="width: auto" />
      </div>

      <AppTable
        :pagination="pagination"
        :columns="returnColumns"
        :data="returnPostData"
        :fetching="loading"
        :searchPlaceHolder="currentPostType?.labels?.search_items || __('Search posts', 'uixpress')"
        :rowClick="openPost"
        v-model:selected="selected"
        :rowRightClick="handleRightClick"
        @search="fetchPostsData"
        @previous="handlePreviousPage"
        @next="handleNextPage"
        @first="handleFirstPage"
        @last="handleLastPage"
      >
        <!--Filters-->
        <template v-slot:right-actions>
          <div class="flex flex-row gap-3 items-stretch">
            <BatchEdit :selected="selected" :fetchPostsData="fetchPostsData" :postType="currentPostType" />

            <TableFilters />

            <a :href="`${appStore.state.adminUrl}post-new.php${maybeReturnPostType}`" :title="__('New post', 'uixpress')">
              <AppButton type="primary" @click="newMenu" class="text-sm">{{ currentPostType?.labels?.new_item || __("Add new", "uixpress") }}</AppButton>
            </a>
          </div>
        </template>

        <!-- Empty slot-->
        <template v-slot:empty>
          <div class="flex flex-col gap-2 p-12 items-center">
            <AppIcon icon="folder_open" style="font-size: 60px" />
            <div class="font-bold text-xl text-zinc-900 dark:text-zinc-100">{{ __("Nothing found", "uixpress") }}</div>
            <div class="text-zinc-400 mb-6">{{ __("When you create new items they will show up here.", "uixpress") }}</div>
            <a :href="`${appStore.state.adminUrl}post-new.php${maybeReturnPostType}`" :title="__('New post', 'uixpress')">
              <AppButton type="primary" @click="newMenu" class="text-sm">{{ currentPostType?.labels?.new_item || __("Add new", "uixpress") }}</AppButton>
            </a>
          </div>
        </template>

        <!-- Table slots -->
        <template v-for="(item, index) in returnColumns" v-slot:[`row-${item.key}`]="{ row }" :key="item.key">
          <div v-if="item.key === 'post_actions'">
            <PostActionList :ref="(el) => setRowActions(el, index, row.id)" :post="row" :data-id="row.id" />
          </div>

          <div v-else-if="item.key === 'date'">
            <div class="text-sm whitespace-nowrap" v-html="row[item.key]"></div>
          </div>

          <div v-else-if="item.key === 'status'">
            <InlineStatusSelect v-model="row[item.key]" :post="row" @updated="(d) => updatePostStatus(row, d)" />
          </div>

          <InlineUserSelect v-else-if="item.key === 'author'" :post="row" v-model="row[item.key]" @updated="updatePostAuthor(row)" />

          <div v-else-if="item.key == 'title'" class="flex flex-row items-center gap-4 relative" :style="`padding-left:${row.depth * 32}px`">
            <AppButton
              type="transparent"
              @click.stop="togglePostExpanded(row)"
              :disabled="!row.children.length"
              :class="!row.children.length ? 'opacity-0' : ''"
              class="text-lg"
              v-if="currentPostType?.name == 'page' && posts.length + 1 <= postsAndChildrenCount"
            >
              <AppIcon :icon="postExpanded(row) || expandAllChildren ? 'expand_more' : 'chevron_right'" />
            </AppButton>

            <div v-if="row.depth && !row.children.length" class="h-full border-r border-zinc-200 dark:border-zinc-700 absolute translate-x-6"></div>
            <InlineImageSelect v-model="row[item.key]" :post="row" :teleportArea="teleportArea" />
            <div class="flex flex-col">
              <div @click.stop class="" v-html="row[item.key].value" :contenteditable="true" @blur="inlineTitleUpdate($event, row.id)" @keydown.prevent.enter="$event.target.blur()"></div>
              <div v-if="returnChildLength(row) > 1" class="text-sm text-zinc-500 font-normal">{{ sprintf(__("%s child items", "uixpress"), returnChildLength(row)) }}</div>
              <div v-if="returnChildLength(row) === 1" class="text-sm text-zinc-500 font-normal">{{ sprintf(__("%s child item", "uixpress"), returnChildLength(row)) }}</div>
            </div>
          </div>

          <div v-else-if="Array.isArray(row[item.key])" class="flex flex-row gap-1 flex-wrap">
            <template v-for="tag in row[item.key]">
              <a
                @click.stop
                :href="tag.url"
                :title="tag.title"
                v-html="tag.title"
                :data-id="tag.id"
                class="p-1 text-xs rounded whitespace-nowrap text-zinc-700 dark:text-zinc-300 hover:text-zinc-900 dark:hover:text-zinc-100 transition-color"
                :class="item.key == 'categories' ? 'bg-indigo-100 dark:bg-indigo-700/30' : 'bg-blue-100 dark:bg-blue-700/30'"
              ></a>
            </template>
          </div>

          <div class="" v-html="row[item.key]" v-else></div>
        </template>
      </AppTable>

      <Confirm ref="confirm" />

      <div ref="teleportArea"></div>

      <div class="fixed bottom-6 left-1/2 -translate-y-1/2 z-[101]">
        <Transition>
          <div class="flex flex-row items-center gap-2 p-2 bg-white dark:bg-zinc-900 border border-zinc-200 dark:border-zinc-700 rounded-xl shadow" v-if="selected.length">
            <AppButton type="transparent" @click="batchPublishPosts" :loading="publishing" :title="__('Publish items', 'uixpress')"><AppIcon icon="publish" class="text-xl" /></AppButton>

            <AppButton type="transparent" @click="batchDuplicatePosts" :loading="duplicating" :title="__('Duplicate items', 'uixpress')"><AppIcon icon="copy" class="text-xl" /></AppButton>

            <AppButton type="transparent" @click="batchDeletePosts" :loading="deleting" :title="__('Delete items', 'uixpress')"><AppIcon icon="delete" class="text-rose-600 text-xl" /></AppButton>

            <div class="h-6 border-r border-zinc-200 dark:border-zinc-700"></div>

            <AppButton type="transparent" @click="selected = []"><AppIcon icon="close" class="text-xl" /></AppButton>
          </div>
        </Transition>
      </div>
    </div>
  </ShadowRoot>
</template>

<style lang="postcss">
tbody .column-title {
  @apply text-zinc-900 dark:text-zinc-100 font-semibold;
}
.uix-comment-count {
  @apply text-sm bg-indigo-500 dark:bg-indigo-600/30 rounded border border-indigo-600 px-1 text-zinc-100;
}
</style>
